import React, { FC, useEffect } from "react";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import { RootState } from "../../redux/RootReducer";
import { fetchSingleUser, fetchUpdateUser } from "../../redux/users/actions";
import { useRouteMatch } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import { FormInput } from "../../components/FormInput/FormInput";
import "./edit-user.scss";

type UpdateUser = {
  discount: string;
};

interface EditUserProps {
  user: any;
  fetchSingleUser: (userId: string) => void;
  fetchUpdateUser: (userId: string, discount: number, history: any) => void;
}

const EditUserContainer: FC<EditUserProps> = ({
  fetchSingleUser,
  fetchUpdateUser,
  user,
}) => {
  const match = useRouteMatch<any>();
  const { userId } = match.params;
  const history = useHistory();
  const { register, handleSubmit, errors, setValue } = useForm();

  useEffect(() => {
    fetchSingleUser(userId);
  }, []);

  useEffect(() => {
    if (user) {
      setValue("discount", user.discount);
    }
  }, [user]);

  const onSubmit = (data: UpdateUser) => {
    fetchUpdateUser(userId, parseInt(data.discount), history);
  };

  return (
    <div className="User-Update">
      <div className="User-Update-Header">
        <p className="User-Update-Header-Title">Edit Sub Category</p>
        <div className="User-Update-Header-Button"></div>
      </div>
      <form className="User-Update-Form" onSubmit={handleSubmit(onSubmit)}>
        <p className="User-Update-Form-Title">Discount:</p>
        <FormInput
          placeholder={"Discount"}
          name={"discount"}
          type={"number"}
          min="0"
          max="100"
          step={"1"}
          errors={errors}
          register={register}
          required={{ required: true }}
        />
        <input
          type="submit"
          className="User-Update-Form-Button"
          value="Update"
        />
      </form>
    </div>
  );
};

const mapStateToProps = (state: RootState) => {
  return {
    user: state.users.user,
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchSingleUser: (userId: string) => dispatch(fetchSingleUser(userId)),
  fetchUpdateUser: (userId: string, discount: number, history: any) =>
    dispatch(fetchUpdateUser(userId, discount, history)),
});

export default connect(mapStateToProps, mapDispatchToProps)(EditUserContainer);
